.App {
  text-align: center;
}

header {
  height: 100px;
  background-color: rgb(27, 27, 27);
  text-align: center;
  margin-bottom: 30px;
}

.header_title {
  padding-top: 25px;
  font-size: 45px;
}

.header_title a {
  text-decoration: none;
  color: rgb(228, 228, 228);
}

h2 {
  margin-bottom: 20px;
}

/*Form component*/
.form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form h2 {
  margin: 0 10px 0 0;
}

.form_input {
  border: none;
  max-width: 150px;
  margin-right: 10px;
  padding-bottom: 5px;
  border-radius: 4px;
  height: 25px;
  font-size: 20px;
  padding: 0 5px;
}

.form_input:focus {
  outline: 0;
}

.form_btn {
  background-color: transparent;
  text-transform: uppercase;
  font-weight: 600;
  padding: 5px;
  border: 3px solid rgb(211, 9, 9);
  cursor: pointer;
  transition: all 0.5s;
}

.form_btn:hover {
  color: #fff;
  background-color: rgb(211, 9, 9);
}

/* Recipes */

.recipes {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-between; */
  margin-bottom: 50px;
  justify-content: flex-start;
}

/* Recipe */

.recipe {
  max-width: 350px;
  background-color: #fff;
  text-align: left;
  margin-top: 25px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: all 0.5s;
  margin-right: 25px;
}

.recipe:nth-child(3n) {
  margin-right: 0;
}

.recipe:hover {
  transform: scale(1.1);
}

.recipe_descrp {
  padding: 20px 5px;
}

.recipe_img {
  width: 350px;
  height: 220px;
  display: block;
}

.recipe_title {
  font-size: 20px;
  text-transform: uppercase;
  color: rgb(187, 5, 5);
  margin-bottom: 12px;
}

.recipe_info {
  margin-bottom: 12px;
  text-transform: uppercase;
  font-weight: 600;
}

.recipe_info span {
  font-weight: 500;
  text-transform: none;
  font-size: 20px;
}

.recipe_btn {
  display: inline-block;
  background-color: transparent;
  text-transform: uppercase;
  padding: 5px;
  border: 2px solid rgb(211, 9, 9);
  color: rgb(211, 9, 9);
  cursor: pointer;
  text-decoration: none;
  margin-top: 15px;
  transition: 0.5s all;
}

.recipe_btn:hover {
  color: #fff;
  background-color: rgb(211, 9, 9);
}

/* Recipe info */
.recipeInfo {
  margin: 0 auto 30px auto;
  width: 500px;
}

.recipeInfo_text {
  text-align: left;
}
.recipeInfo_img {
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  margin-bottom: 20px;
}
.recipeInfo_title {
  font-size: 40px;
  text-transform: uppercase;
  margin-bottom: 20px;
  margin-top: 20px;
}

.recipeInfo_publisher {
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 15px;
}

.recipeInfo_publisher span {
  font-weight: 500;
  text-transform: none;
  font-size: 20px;
}

.recipeInfo_website a {
  text-decoration: none;
  color: black;
}

.recipeInfo_website {
  font-size: 20px;
}

.recipeInfo_website a:hover {
  text-decoration: underline;
}

.recipeInfo_recipe {
  margin: 0 auto;
  margin-top: 20px;
  text-align: center;
}

.recipeInfo_recipe span {
  display: inline-block;
  font-size: 35px;
  font-weight: 700;
  margin-bottom: 10px;
}

.recipeInfo_recipe_text {
  text-align: left;
}
